import styled from "styled-components";
import { Link, withRouter } from "react-router-dom";

const SHeader = styled.header`
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0px 10px;
  background-color: rgba(20, 20, 20, 0.8);
  box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.8);
  z-index: 2;
`;

//npa add styled-components ì´í›„ ì•„ë˜ì™€ ê°™ì´ ìŠ¤íƒ€ì¼ì„ ì…ë ¥í•  ìˆ˜ ìˆê²Œëœë‹¤.
const List = styled.ul`
  display: flex;
`;

const Item = styled.li`
  height: 50px;
  width: 50px;
  text-align: center;
  border-bottom: 5px solid
    ${(props) => (props.current ? "#3498db" : "transparent")};
  transition: border-bottom 0.5s ease-in-out;
`;

const SLink = styled(Link)`
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
`;
//react router domì˜ Linkë¥¼ ì‚¬ìš©í•˜ëŠ” ê²½ìš°ì—ëŠ” ìœ„ì™€ ê°™ì´ ìŠ¤íƒ€ì¼ì„ ì¤„ ìˆ˜ ìˆë‹¤.

function Header({ location: { pathname } }) {
  return (
    <SHeader>
      <List>
        <Item current={pathname === "/wooflix"}>
          <SLink to="/wooflix">Movie</SLink>
        </Item>
        <Item current={pathname === "/wooflix/TV"}>
          <SLink to="/wooflix/TV">TV</SLink>
        </Item>
        <Item current={pathname === "/wooflix/Search"}>
          <SLink to="/wooflix/Search">Search</SLink>
        </Item>
      </List>
    </SHeader>
  );
}

export default withRouter(Header);

