import React from "react";
import { movieApi, tvApi } from "../../api";
import SearchPresenter from "./SearchPresenter";

class SearchContainer extends React.Component {
  state = {
    movieResults: null,
    tvResults: null,
    searchTerm: "",
    error: null,
    loading: false, //ì‚¬ìš©ìê°€ ê²€ìƒ‰í•˜ê¸°ë¥¼ ê¸°ë‹¤ë ¸ë‹¤ ë¡œë”©í•˜ê¸° ìœ„í•¨.
    lastTerm: "",
  };

  handleSubmit = (event) => {
    event.preventDefault(); //ì›ë˜ enterë¥¼ ì¹˜ë©´ searchë‚´ìš©ì´ submitë˜ë‚˜, ì´ë•Œ í™”ë©´ì„ refreshí•œë‹¤. ì´ë¥¼ ë§‰ê¸°ìœ„í•´ ë„£ëŠ”ë‹¤.
    const { searchTerm } = this.state;
    if (searchTerm !== "") {
      this.searchbyTerm();
    }
  };

  isenterkey = async (event) => {
    if (event.key === "Enter")
      await this.setState({ lastTerm: event.target.value });
  };

  updateTerm = (event) => {
    //ì´ eventë¥¼ console.log í•´ì„œ ì–´ë– í•œ ì •ë³´ê°€ ì „ë‹¬ë˜ëŠ”ì§€ í™•ì¸í•˜ê³  ê·¸ì— ë§ì¶° ë¡œì§ì„ ì„¤ê³„í•œë‹¤.
    const {
      target: { value },
    } = event;
    this.setState({ searchTerm: value }); //ì´ë¶€ë¶„ì€ ê·¸ëƒ¥ ê³µì‹ì²˜ëŸ¼ ì™¸ì›Œì£¼ëŠ”ê²Œ ì¢‹ê² ë‹¤..
  };

  searchbyTerm = async () => {
    const { searchTerm } = this.state;
    try {
      this.setState({ loading: true });
      const {
        data: { results: movieResults },
      } = await movieApi.search(searchTerm);
      const {
        data: { results: tvResults },
      } = await tvApi.search(searchTerm);
      this.setState({ movieResults, tvResults });
    } catch {
      this.setState({ error: "Can't find results." });
    } finally {
      this.setState({ loading: false });
    }
  };

  render() {
    const {
      movieResults,
      tvResults,
      searchTerm,
      error,
      loading,
      lastTerm,
    } = this.state;
    return (
      <SearchPresenter
        error={error}
        loading={loading}
        movieResults={movieResults}
        tvResults={tvResults}
        searchTerm={searchTerm}
        lastTerm={lastTerm}
        handleSubmit={this.handleSubmit}
        updateTerm={this.updateTerm}
        isenterkey={this.isenterkey}
      />
    );
  }
}

export default SearchContainer;
